import prisma from "@/app/lib/db";
import NextAuth, { AuthOptions } from "next-auth";
import KeycloakProvider from "next-auth/providers/keycloak";
import path from "path";
import fs from "fs";
import { stripe } from "@/app/lib/stripe";

const keycloak = KeycloakProvider({
    clientId: `${process.env.KEYCLOAK_ID}`,
    clientSecret: `${process.env.KEYCLOAK_SECRET}`,
    issuer: `${process.env.KEYCLOAK_ISSUER}`,
});

export const authOptions: AuthOptions = {
    providers: [
        keycloak
    ],
    secret: process.env.AUTH_SECRET,
    callbacks: {
        // async signIn({user, account, profile}) {

        //     try {

        //         await prisma.user.upsert({
        //             where: { email: profile?.email },
        //             update: {
        //                 keycloakUserId: profile?.sub,
        //                 email: profile?.email,
        //                 fullname: profile?.name,
        //                 image: profile?.image
        //             },
        //             create: {
        //                 keycloakUserId: profile?.sub,
        //                 email: profile?.email,
        //                 fullname: profile?.name,
        //                 image: profile?.image
        //             },
        //           });

        //         return true;
        //     } catch (error) {
        //         const logPath = path.join(process.cwd(), "debug.log");
        //         fs.appendFileSync(logPath, `${new Date().toISOString()} - ${JSON.stringify(error)}\n`);

        //         return false;
        //     }

        // },
        async jwt({ token, account, profile }) {
            if (account) {
                token.id_token = account.id_token; // Include id_token in the token
            }

            if (account) {
                token = { ...token, access_token: account.access_token };
                token.exp = account.expires_at;
                token.id_token = account.id_token ?? '';
            }
            return token;
        },
        async session({ session, token, user }) {
            if (session) {
                session.access_token = token.access_token as string;
                session.user.id = token.sub
                // session.user = await getUserInfo(session.access_token);
            }
            return {
                ...session,
                token: token,
            };
        },
    },
    pages: {
        signIn: "/dashboard"
    },
    session: {
        strategy: "jwt"
    },
    events: {
        async signOut({ token }) {
            const { id_token } = token;
            console.log('ROUTE')

            try {
                const params = new URLSearchParams();
                params.append('id_token_hint', id_token as string);

                await fetch(`${keycloak?.options?.issuer}/protocol/openid-connect/logout?${params.toString()}`);
                if (typeof window !== 'undefined') window.location.href = '/login';

            } catch (error) {
                console.error('Unable to perform post-logout handshake');
            }

        },
        createUser: async (message) => {
            const userId = message.user.id;
            const email = message.user.email;
            const name = message.user.name;

            if (!userId || !email) {
                return;
            }

            const stripeCustomer = await stripe.customers.create({
                email,
                name: name ?? undefined,
            })

            await prisma.user.update({
                where: {
                    keycloakUserId: userId,
                },
                data: {
                    stripeCustomerId: stripeCustomer.id
                }
            })
        }
    }
}

const handler = NextAuth(authOptions);

export { handler as GET, handler as POST }